import { Constants } from "../Data/constants";
// import * as PF from "pathfinding";
const {ccclass, property} = cc._decorator;

@ccclass
export default class Bee extends cc.Component {

    @property(cc.RigidBody)
    rigidBody: cc.RigidBody = null;

    protected start(): void {
        this.beginContact();
      
    }


    private beginContact(): void {
        this.rigidBody.onBeginContact = (c,s,o) => {
            if(o.tag === 4) {
                console.log("collide");
            }
            
        }
    }


    private attack(): void {
            let targetPosition = Constants.CharacterPos;
            let currentPosition = this.node.getPosition();
            currentPosition.lerp(targetPosition, 0.01, currentPosition);
            this.node.setPosition(currentPosition);
    }


    protected update(dt: number): void {
        if(window.isDraw) {
            this.attack();
        }
    }

}
